.choise-box__aside-inner { max-width: 375px; margin-left: auto; padding: 35px 20px 70px 30px; display: flex; flex-direction: column; align-items: flex-start; height: 100%; }

@media (max-width: 1054px) { .choise-box__aside-inner { padding-left: 20px; padding-right: 20px; max-width: none; padding-bottom: 30px; } }

.choise-box__name { font-weight: 500; font-size: 16px; line-height: 130%; margin-bottom: 20px; color: #fff; }

.choise-menu { margin-top: auto; margin-bottom: auto; font-size: 44px; line-height: 120%; color: rgba(255, 255, 255, 0.7); }

@media (max-width: 1439px) { .choise-menu { font-size: 36px; } }

.choise-menu__btn { font-style: normal; overflow: hidden; position: relative; transform: skew(0deg); padding-left: 0; transition: padding 0.3s 1.1s ease-in-out, transform 0.1s 1.1s ease-in-out, color 0.3s 1.1s ease-in-out; }

.choise-menu__btn:before { content: "→ "; position: absolute; left: 0; top: 50%; flex-shrink: 0; margin-right: 10px; transform: translateX(-55px) translateY(-50%); transition: transform 0.3s 1.1s ease-in-out; }

.choise-menu__btn:hover { color: white; transition: padding 0.3s 1.1s ease-in-out, transform 0.1s ease-in-out, color 0.3s ease-in-out; }

.choise-menu__btn.active { color: white; padding-left: 55px; transform: skew(-15deg); transition: padding 0.3s 1.1s ease-in-out, transform 0.1s ease-in-out, color 0.3s ease-in-out; }

.choise-menu__btn.active:before { margin-left: 0; color: #fff; transform: translateX(0) translateY(-50%); transition: transform 0.3s 1.1s ease-in-out; }

.choise-menu__item { margin: 20px 0; }

.choise-menu__item.active .choise-menu__btn { font-style: italic; color: white; padding-left: 55px; }

.choise-menu__item.active .choise-menu__btn:before { margin-left: 0; color: #fff; transform: translateX(0) translateY(-50%); }
